"use client"; 

import { useState } from "react";
import { Search, ShoppingBag, Info } from "lucide-react";
import { Store, Product } from "@/types";

// --- IMPORT THE NEW SPLIT PARTS ---
import StoreHeader from "./store/StoreHeader";
import CategoryFilter from "./store/CategoryFilter";
import ProductGrid from "./store/ProductGrid";
import StoreInfoModal from "./store/StoreInfoModal";
import CheckoutModal from "./store/CheckoutModal";
// We will reuse the Premium Modal Logic inside here or move it to a file later
import Image from "next/image";
import { X, ChevronLeft, ChevronRight, Plus } from "lucide-react"; // Icons for modal

// Define Category Type locally for now
type Category = { id: string; name: string };

interface StoreFrontProps {
  store: Store;
  products: Product[];
  categories: Category[]; // <--- NEW PROP
}

export default function StoreFront({ store, products, categories }: StoreFrontProps) {
  // State
  const [searchQuery, setSearchQuery] = useState("");
  const [activeCategory, setActiveCategory] = useState("all");
  const [cart, setCart] = useState<{ product: Product; qty: number }[]>([]);
  
  // Modals
  const [isCheckoutOpen, setIsCheckoutOpen] = useState(false);
  const [isInfoOpen, setIsInfoOpen] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null);
  const [activeImageIndex, setActiveImageIndex] = useState(0);

  // --- FILTER LOGIC ---
  const filteredProducts = products.filter((p) => {
    const matchesSearch = p.name.toLowerCase().includes(searchQuery.toLowerCase());
    // In future, we match p.category_id === activeCategory
    // For now, since we haven't linked products yet, we just show all if 'all'
    const matchesCategory = activeCategory === "all" ? true : (p as any).category_id === activeCategory;
    
    return matchesSearch && matchesCategory;
  });

  // --- CART LOGIC ---
  const addToCart = (product: Product, e?: React.MouseEvent) => {
    if (e) e.stopPropagation();
    setCart((prev) => {
      const existing = prev.find((item) => item.product.id === product.id);
      if (existing) return prev.map((item) => item.product.id === product.id ? { ...item, qty: item.qty + 1 } : item);
      return [...prev, { product, qty: 1 }];
    });
  };

  const removeFromCart = (id: string) => setCart(prev => prev.filter(item => item.product.id !== id));
  const cartCount = cart.reduce((sum, item) => sum + item.qty, 0);
  const cartTotal = cart.reduce((sum, item) => sum + item.product.price * item.qty, 0);

  return (
    <div className="bg-gray-50 min-h-screen font-sans pb-32">
      
      {/* 1. Header */}
      <StoreHeader store={store} />

      {/* 2. Search & Info Bar */}
      <div className="px-4 mt-6 mb-4 flex gap-3">
        <div className="relative flex-1">
          <Search className="absolute left-3 top-3 text-gray-400 w-5 h-5" />
          <input 
            type="text" 
            placeholder={`Search ${store.name}...`}
            className="w-full bg-white pl-10 pr-4 py-3 rounded-xl text-sm shadow-sm border border-gray-100 focus:outline-none focus:ring-2 focus:ring-gray-900"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
        <button onClick={() => setIsInfoOpen(true)} className="bg-white text-gray-700 p-3 rounded-xl shadow-sm border border-gray-100">
           <Info size={20} />
        </button>
        <button onClick={() => setIsCheckoutOpen(true)} className="bg-gray-900 text-white p-3 rounded-xl shadow-lg relative">
          <ShoppingBag size={20} />
          {cartCount > 0 && (
            <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-5 w-5 flex items-center justify-center rounded-full border-2 border-white">
              {cartCount}
            </span>
          )}
        </button>
      </div>

      {/* 3. Category Rail (Only if categories exist) */}
      {categories.length > 0 && (
        <CategoryFilter 
          categories={categories} 
          activeCategory={activeCategory} 
          onSelect={setActiveCategory} 
        />
      )}

      {/* 4. Product Grid */}
      <div className="mt-4">
        <ProductGrid 
          products={filteredProducts} 
          onProductClick={(p) => { setSelectedProduct(p); setActiveImageIndex(0); }} 
          onAddToCart={addToCart} 
        />
      </div>

      {/* 5. Sticky Cart */}
      {cart.length > 0 && !isCheckoutOpen && (
        <div className="fixed bottom-5 left-4 right-4 bg-gray-900 text-white p-4 rounded-2xl shadow-2xl flex justify-between items-center z-40 cursor-pointer animate-in slide-in-from-bottom-5" onClick={() => setIsCheckoutOpen(true)}>
           <div className="flex flex-col">
              <span className="text-xs text-gray-400">{cartCount} Items</span>
              <span className="font-bold text-lg">â‚¦{cartTotal.toLocaleString()}</span>
           </div>
           <span className="bg-white text-gray-900 px-6 py-2 rounded-xl font-bold text-sm hover:bg-gray-100">Checkout</span>
        </div>
      )}

      {/* --- MODALS --- */}
      <CheckoutModal isOpen={isCheckoutOpen} onClose={() => setIsCheckoutOpen(false)} cart={cart} store={store} onRemoveItem={removeFromCart} />
      
      <StoreInfoModal isOpen={isInfoOpen} onClose={() => setIsInfoOpen(false)} store={store} productCount={products.length} />

      {/* Premium Product Detail Modal (Kept inline for now due to complexity, but cleaned up) */}
      {selectedProduct && (
        <div className="fixed inset-0 z-50 flex items-end md:items-center justify-center bg-black/70 backdrop-blur-sm p-0 md:p-4 animate-in fade-in">
          <div className="bg-white w-full md:max-w-4xl h-[90vh] md:h-[80vh] rounded-t-3xl md:rounded-3xl shadow-2xl flex flex-col md:flex-row relative overflow-hidden">
             <button onClick={() => setSelectedProduct(null)} className="absolute top-4 right-4 z-30 bg-white/80 p-2 rounded-full shadow-sm"><X size={20} /></button>
             
             {/* Left: Image */}
             <div className="w-full md:w-1/2 bg-gray-100 relative h-72 md:h-full shrink-0">
               {selectedProduct.image_urls && selectedProduct.image_urls.length > 0 && (
                 <Image src={selectedProduct.image_urls[activeImageIndex]} alt="" fill className="object-cover" />
               )}
               {/* Mobile Dots */}
               <div className="absolute bottom-4 left-0 right-0 flex justify-center gap-2 md:hidden">
                 {selectedProduct.image_urls?.map((_, i) => (
                   <div key={i} className={`w-2 h-2 rounded-full ${i === activeImageIndex ? 'bg-white' : 'bg-white/50'}`} />
                 ))}
               </div>
             </div>

             {/* Right: Info */}
             <div className="w-full md:w-1/2 flex flex-col h-full bg-white overflow-hidden">
                <div className="flex-1 overflow-y-auto p-6 md:p-8">
                   <h2 className="text-2xl font-bold text-gray-900">{selectedProduct.name}</h2>
                   <p className="text-2xl font-bold text-emerald-600 mt-2">â‚¦{selectedProduct.price.toLocaleString()}</p>
                   
                   {/* Desktop Thumbnails */}
                   <div className="hidden md:flex gap-2 mt-4">
                     {selectedProduct.image_urls?.map((url, i) => (
                       <button key={i} onClick={() => setActiveImageIndex(i)} className={`w-14 h-14 relative rounded-lg overflow-hidden border-2 ${i === activeImageIndex ? 'border-black' : 'border-transparent'}`}>
                         <Image src={url} alt="" fill className="object-cover" />
                       </button>
                     ))}
                   </div>

                   <div className="mt-6 border-t border-gray-100 pt-6">
                     <h3 className="text-sm font-bold uppercase mb-2">Description</h3>
                     <p className="text-gray-500 text-sm leading-relaxed">{selectedProduct.description}</p>
                   </div>
                </div>
                <div className="p-4 border-t border-gray-100 bg-white shrink-0">
                   <button onClick={() => addToCart(selectedProduct!)} className="w-full bg-gray-900 text-white py-4 rounded-xl font-bold">Add to Bag</button>
                </div>
             </div>
          </div>
        </div>
      )}

    </div>
  );
}