"use client";
import { X, MapPin, Globe, Calendar, Package, MessageCircle } from "lucide-react";
import { Store } from "@/types";
import Image from "next/image";

interface StoreInfoModalProps {
  store: Store;
  isOpen: boolean;
  onClose: () => void;
  productCount: number;
}

export default function StoreInfoModal({ store, isOpen, onClose, productCount }: StoreInfoModalProps) {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-end sm:items-center justify-center bg-black/60 backdrop-blur-sm p-4 animate-in fade-in">
      <div className="bg-white w-full max-w-sm rounded-3xl overflow-hidden shadow-2xl animate-in slide-in-from-bottom-10">
        
        {/* Cover & Logo */}
        <div className="relative h-32 bg-gray-900">
           {store.cover_image_url && <Image src={store.cover_image_url} alt="" fill className="object-cover opacity-80" />}
           <button onClick={onClose} className="absolute top-4 right-4 bg-black/20 text-white p-2 rounded-full hover:bg-black/40 backdrop-blur-md"><X size={20}/></button>
           
           <div className="absolute -bottom-10 left-6">
             <div className="w-24 h-24 rounded-full border-4 border-white bg-white shadow-md relative overflow-hidden">
                {store.logo_url ? <Image src={store.logo_url} alt="" fill className="object-cover" /> : <div className="w-full h-full bg-gray-100" />}
             </div>
           </div>
        </div>

        <div className="pt-12 px-6 pb-8">
           <h2 className="text-2xl font-bold text-gray-900">{store.name}</h2>
           <div className="flex items-center gap-1 text-gray-500 text-sm mt-1">
             <MapPin size={14} /> {store.location}
           </div>

           {/* Stats Row */}
           <div className="flex gap-4 mt-6 border-y border-gray-100 py-4">
              <div className="text-center">
                 <p className="text-xl font-bold text-gray-900">{productCount}</p>
                 <p className="text-xs text-gray-400 uppercase tracking-wider">Items</p>
              </div>
              <div className="text-center border-l border-gray-100 pl-4">
                 <p className="text-xl font-bold text-gray-900">100%</p>
                 <p className="text-xs text-gray-400 uppercase tracking-wider">Reliable</p>
              </div>
           </div>

           {/* Bio */}
           <div className="mt-6">
             <h3 className="text-sm font-bold text-gray-900 uppercase mb-2">About Store</h3>
             <p className="text-gray-500 text-sm leading-relaxed">{store.description || "No description provided."}</p>
           </div>

           {/* Socials */}
           <div className="mt-6 space-y-3">
             {store.instagram_url && (
               <a href={store.instagram_url} target="_blank" className="flex items-center gap-3 p-3 bg-pink-50 text-pink-700 rounded-xl text-sm font-bold hover:bg-pink-100 transition">
                 <Globe size={18} /> Instagram
               </a>
             )}
             {store.tiktok_url && (
               <a href={store.tiktok_url} target="_blank" className="flex items-center gap-3 p-3 bg-gray-100 text-gray-900 rounded-xl text-sm font-bold hover:bg-gray-200 transition">
                 <Globe size={18} /> TikTok
               </a>
             )}
              <a href={`https://wa.me/${store.whatsapp_number}`} target="_blank" className="flex items-center gap-3 p-3 bg-green-50 text-green-700 rounded-xl text-sm font-bold hover:bg-green-100 transition">
                 <MessageCircle size={18} /> WhatsApp
               </a>
           </div>
        </div>
      </div>
    </div>
  );
}